import React, { useEffect, useState } from "react";
import "./Update.scss";

const Update = ({ data, show, handleSubmit }) => {

    const [dataUpdate, setDataUpdate] = useState({});
    const [value, setValue] = useState('');
    const [showP, setShowP] = useState(false)

    useEffect(() => {
        setDataUpdate(data);
        setValue(data.description);
    }, [data]);

    useEffect(() => {
        setShowP(show)
    }, [show, data])

    const hidden = () => {
        setShowP(false)
    }
    return (
        <div className="update" style={!showP ? { display: "none" } : { display: 'block' }}>
            <div className="update__title">Chnh sa thng tin</div>
            <div className="update__box">
                <div className="update__group">
                    <label className="update__label" htmlFor="name">H v tn</label>
                    <input className="update__input" type="text" id="name" value={dataUpdate.name} onChange={(e) => { setDataUpdate({ ...dataUpdate, name: e.target.value }) }} />
                </div>
                <div className="update__group">
                    <label className="update__label" htmlFor="phone-number">S in thoi</label>
                    <input className="update__input" type="text" id="phone-number" value={dataUpdate.phone} onChange={(e) => { setDataUpdate({ ...dataUpdate, phone: e.target.value }) }} />
                </div>
                <div className="update__group">
                    <label className="update__label" htmlFor="email">Email</label>
                    <input className="update__input" type="text" id="email" value={dataUpdate.email} onChange={(e) => { setDataUpdate({ ...dataUpdate, email: e.target.value }) }} />
                </div>
                <div className="update__group">
                    <label className="update__label" htmlFor="position">V tr ng tuyn</label>
                    <input className="update__input" type="text" id="position" value={dataUpdate.position} onChange={(e) => { setDataUpdate({ ...dataUpdate, position: e.target.value }) }} />
                </div>
                <div className="update__group">
                    <label className="update__label" htmlFor="status">Trng thi</label>
                    <select className="update__input" id="status" value={value} onChange={(e) => { setValue(e.target.value); setDataUpdate({ ...dataUpdate, description: e.target.value }) }}>
                        <option value=" hy"> hy</option>
                        <option value=" phng vn"> phng vn</option>
                        <option value="Cha phng vn">Cha phng vn</option>
                        <option value="ang xt duyt">ang xt duyt</option>
                        <option value=" c nhn"> c nhn</option>
                    </select>
                </div>
                <input className="update__input" type="hidden" value={dataUpdate.cv} />
                <input className="update__input" type="hidden" value={dataUpdate.id} />
            </div>
            <div className="update__cta">
                <button type="button" className="button update__submit" onClick={() => { handleSubmit(dataUpdate); hidden() }}>Lu</button>
                <button type="button" className="button red update__submit" onClick={hidden}>Hy</button>
            </div>
        </div>
    )
}

export default Update